.deposit-web {
    display: flex;
    flex-direction: column;
    justify-content: center;

    border-radius: 10px;
    border: 1px solid #eee;
    background: #fff;

    padding: 20px 15px 17px 15px;

    #header {
        color: #000;
        font-family: "Roboto";
        font-size: 20px;
        font-style: normal;
        font-weight: 500;

        margin-bottom: 14px;
        margin-left: 7px;
    }

    .input {
        display: flex;
        align-items: center;

        padding: 30px 20px;

        border-radius: 10px;
        border: 1px solid #f1f2f7;

        background: #f4f5fa;

        .left {
            input {
                color: #9aa0bb;
                font-family: "Roboto";
                font-size: 32px;
                font-style: normal;
                font-weight: 400;

                background: none;
                border: none;

                margin-bottom: 14px;

                &:focus {
                    outline: none;
                }
            }

            #hint {
                color: #787d91;
                font-family: "Roboto";
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
            }
        }

        .right {
            display: flex;
            flex-direction: column;

            img {
                margin-bottom: 10px;
            }

            .good-balance-text {
                width: fit-content;

                display: flex;

                #balance {
                    color: #787d91;
                    font-family: "Roboto";
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 400;
                }

                #max {
                    margin-left: 5px;

                    color: #0052ff;
                    font-family: "Roboto";
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 500;
                }
            }

            .bad-balance-text {
                color: #cf202f;
                font-family: "Roboto";
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                text-align: right;
            }
        }
    }

    .empty-balance {
        margin-top: 15px;

        background: #f4f5fa;

        border-radius: 100px;

        padding: 23px 150px;

        color: #7b7b7b;
        text-align: center;
        font-family: "Roboto";
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
    }

    .good-balance {
        margin-top: 15px;

        background: #c3f53c;

        border-radius: 100px;

        padding: 23px 150px;

        color: #000;
        text-align: center;
        font-family: "Roboto";
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
    }

    .bad-balance {
        margin-top: 15px;

        #subtext {
            width: 470px;

            color: #3c3c3c;
            font-family: "Roboto";
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            margin-bottom: 20px;
        }

        #uniswap {
            text-decoration: none;
            color: #0052ff;
            font-family: "Roboto";
            font-size: 16px;
            font-style: normal;
            font-weight: 700;

            &:hover {
                opacity: 0.8;
            }
        }
    }
}
